import { Box, CardMedia, IconButton } from '@mui/material';
import { Edit as EditIcon } from '@mui/icons-material';
import { DEFAULT_PLACEHOLDER } from '@/constants/common';
import AdvertisementStats from './AdvertisementStats';

// Styled components
const ImageWrapper = {
  position: 'relative',
  width: '100%',
  paddingTop: '100%',
  borderRadius: 1,
  overflow: 'hidden',
} as const;

const StyledCardMedia = {
  position: 'absolute',
  top: 0,
  left: 0,
  width: '100%',
  height: '100%',
  objectFit: 'cover',
  borderRadius: 1,
} as const;

const EditButton = {
  position: 'absolute',
  top: 12,
  right: 12,
  bgcolor: 'rgba(255, 255, 255, 0.9)',
  boxShadow: '0 2px 8px rgba(0, 0, 0, 0.1)',
  '&:hover': {
    bgcolor: 'rgba(255, 255, 255, 1)',
  },
} as const;

interface AdvertisementImageProps {
  imageUrl?: string;
  name?: string;
  views?: number;
  likes?: number;
  onEditImage: () => void;
}

function AdvertisementImage({
  imageUrl,
  name,
  views,
  likes,
  onEditImage,
}: AdvertisementImageProps) {
  return (
    <Box sx={ImageWrapper}>
      <CardMedia
        component="img"
        image={imageUrl || DEFAULT_PLACEHOLDER}
        alt={name}
        sx={StyledCardMedia}
      />
      <IconButton onClick={onEditImage} sx={EditButton}>
        <EditIcon />
      </IconButton>
      <AdvertisementStats views={views} likes={likes} />
    </Box>
  );
}

export default AdvertisementImage;
