import { Box, Card, Typography, Button } from '@mui/material';
import {
  Edit as EditIcon,
  KeyboardArrowDown,
  KeyboardArrowUp,
  Check as CheckIcon,
  Close as CloseIcon,
} from '@mui/icons-material';
import useAdvertisement from './hooks/useAdvertisement';
import Layout from '@layouts/layout';
import ErrorComponent from '@components/ErrorComponent';
import ImageUpdateModal from './components/ImageUpdateModal';
import { FIELD_LIMITS } from '@/constants/fieldLimits';
import { useAdvertisementEdit } from './hooks/useAdvertisementEdit';
import AdvertisementImage from './components/AdvertisementImage';
import ActionButton from '@components/ActionButton';
import EditableField from './components/EditableField';

// Styled components
const StyledCard = {
  display: 'flex',
  flexDirection: { xs: 'column', md: 'row' },
  padding: { xs: 3, sm: 3, md: 4 },
  maxWidth: { xs: '100%', sm: '600px', md: '1200px' },
  width: { xs: 'auto', sm: 'calc(100% - 32px)' },
  margin: '24px auto',
  boxShadow: 'none',
  position: 'relative',
  bgcolor: '#ffffff',
  overflow: 'hidden',
} as const;

const ImageContainer = {
  width: { xs: '100%', md: '500px' },
  flexShrink: 0,
  alignSelf: 'flex-start',
  display: 'flex',
  flexDirection: 'column',
  gap: 2,
};

const ButtonsContainer = {
  display: 'flex',
  gap: 1,
  justifyContent: { xs: 'flex-start', md: 'center' },
  mt: { xs: 2, md: 0 },
  position: { md: 'absolute' },
  top: { md: 16 },
  right: { md: 16 },
};

const ContentContainer = {
  flex: 1,
  minWidth: 0,
  paddingTop: { xs: 3, md: 4 },
  paddingLeft: { xs: 0, md: 4 },
  paddingRight: { xs: 0, md: 2 },
  display: 'flex',
  flexDirection: 'column',
  overflow: 'auto',
  maxHeight: { md: '800px' },
};

const FieldsContainer = {
  mb: 4,
  width: '100%',
};

const PriceTypography = {
  fontWeight: 700,
  fontSize: { xs: '24px', sm: '28px', md: '32px' },
  color: 'custom.price',
};

const getDescriptionBoxStyles = (isEditMode: boolean) => ({
  position: 'relative',
  width: '100%',
  padding: 1,
  margin: -1,
  cursor: isEditMode ? 'pointer' : 'default',
  '&:hover': {
    bgcolor: isEditMode ? 'rgba(0, 0, 0, 0.02)' : 'transparent',
    borderRadius: isEditMode ? 1 : 0,
  },
});

const getDescriptionTypographyStyles = (isExpanded: boolean) => ({
  fontSize: { xs: '16px', sm: '16px', md: '18px' },
  lineHeight: 1.6,
  color: 'custom.warmTones.body',
  letterSpacing: '0.2px',
  mb: 1,
  overflow: 'hidden',
  display: '-webkit-box',
  WebkitLineClamp: isExpanded ? 'unset' : 3,
  WebkitBoxOrient: 'vertical',
});

const ShowMoreButton = {
  textTransform: 'none',
  color: 'custom.warmTones.body',
  padding: '4px 8px',
  minWidth: 'auto',
  '&:hover': {
    background: 'rgba(0, 0, 0, 0.04)',
  },
  display: 'flex',
  alignItems: 'center',
  gap: 0.5,
} as const;

const EmptyDescriptionBox = {
  p: 2,
  bgcolor: 'rgba(0,0,0,0.02)',
  borderRadius: 1,
  border: '1px dashed rgba(0,0,0,0.1)',
};

function AdvertisementPage() {
  const { advertisement, isLoading, error, handleUpdate, refetch } =
    useAdvertisement();
  const { imageUrl, name, price, description, views, likes } =
    advertisement || {};

  const {
    editingField,
    editedFields: {
      title: editedTitle,
      price: editedPrice,
      description: editedDescription,
    },
    isEditMode,
    imageModalOpen,
    isImageLoading,
    isDescriptionExpanded,
    setImageModalOpen,
    handleStartEdit,
    handleSave,
    handleCancel,
    handleImageUpdate,
    toggleEditMode,
    handleFieldChange,
    toggleDescription,
    titleInputRef,
    priceInputRef,
    descriptionInputRef,
    validationErrors,
    getFieldTip,
  } = useAdvertisementEdit({
    handleUpdate,
    refetch,
    name,
    price,
    description,
  });

  return (
    <Layout>
      {isLoading && <div>Loading...</div>}
      {error && <ErrorComponent />}
      {!isLoading && !error && (
        <Card sx={StyledCard}>
          <Box sx={ImageContainer}>
            <AdvertisementImage
              imageUrl={imageUrl}
              name={name}
              views={views}
              likes={likes}
              onEditImage={() => setImageModalOpen(true)}
            />
          </Box>

          <Box sx={ButtonsContainer}>
            {isEditMode ? (
              <>
                <ActionButton
                  variant="success"
                  icon={<CheckIcon />}
                  onClick={handleSave}
                  sx={{
                    minWidth: '140px',
                    height: '40px',
                  }}
                >
                  Save Changes
                </ActionButton>
                <ActionButton
                  variant="outline"
                  icon={<CloseIcon />}
                  onClick={handleCancel}
                  sx={{
                    minWidth: '100px',
                    height: '40px',
                  }}
                >
                  Cancel
                </ActionButton>
              </>
            ) : (
              <ActionButton
                variant="secondary"
                icon={<EditIcon />}
                onClick={toggleEditMode}
                sx={{
                  minWidth: '120px',
                  height: '40px',
                }}
              >
                Edit Details
              </ActionButton>
            )}
          </Box>

          <Box sx={ContentContainer}>
            <Box sx={FieldsContainer}>
              <EditableField
                isEditing={editingField === 'title'}
                isEditMode={isEditMode}
                onStartEdit={() => handleStartEdit('title')}
                value={editedTitle || ''}
                onChange={handleFieldChange('title')}
                inputRef={titleInputRef}
                error={validationErrors.title}
                helperText={validationErrors.title || getFieldTip('title')}
              >
                <Typography variant="pageTitle" component="h1">
                  {editedTitle || name}
                </Typography>
              </EditableField>

              <EditableField
                isEditing={editingField === 'price'}
                isEditMode={isEditMode}
                onStartEdit={() => handleStartEdit('price')}
                value={editedPrice || ''}
                onChange={handleFieldChange('price')}
                inputRef={priceInputRef}
                error={validationErrors.price}
                helperText={validationErrors.price || getFieldTip('price')}
              >
                <Typography sx={PriceTypography}>
                  ${editedPrice || price}
                </Typography>
              </EditableField>

              <EditableField
                isEditing={editingField === 'description'}
                isEditMode={isEditMode}
                onStartEdit={() => handleStartEdit('description')}
                value={editedDescription || ''}
                onChange={handleFieldChange('description')}
                inputRef={descriptionInputRef}
                error={validationErrors.description}
                helperText={`${(editedDescription || '').length}/${FIELD_LIMITS.description.max}`}
                multiline
                minRows={4}
                maxRows={10}
                inputProps={{
                  placeholder:
                    'Add a description to help buyers understand your product better',
                }}
              >
                <Box
                  sx={getDescriptionBoxStyles(isEditMode)}
                  onClick={() => isEditMode && handleStartEdit('description')}
                >
                  {description ? (
                    <>
                      <Typography
                        sx={getDescriptionTypographyStyles(
                          isDescriptionExpanded,
                        )}
                      >
                        {editedDescription || description}
                      </Typography>
                      {description.length > 600 && (
                        <Button
                          onClick={(e) => {
                            e.stopPropagation();
                            toggleDescription(e);
                          }}
                          sx={ShowMoreButton}
                        >
                          {isDescriptionExpanded ? (
                            <>
                              Show less
                              <KeyboardArrowUp fontSize="small" />
                            </>
                          ) : (
                            <>
                              Show more
                              <KeyboardArrowDown fontSize="small" />
                            </>
                          )}
                        </Button>
                      )}
                    </>
                  ) : (
                    <Box sx={EmptyDescriptionBox}>
                      <Typography color="text.secondary">
                        Add a description to help buyers understand your product
                        better
                      </Typography>
                    </Box>
                  )}
                </Box>
              </EditableField>
            </Box>
          </Box>
        </Card>
      )}

      <ImageUpdateModal
        open={imageModalOpen}
        onClose={() => setImageModalOpen(false)}
        onUpdate={handleImageUpdate}
        isLoading={isImageLoading}
      />
    </Layout>
  );
}

export default AdvertisementPage;
