import { Box, TextField } from '@mui/material';
import { ReactNode, ChangeEvent } from 'react';

interface EditableFieldProps {
  isEditing: boolean;
  isEditMode: boolean;
  onStartEdit: () => void;
  value: string;
  onChange: (e: ChangeEvent<HTMLInputElement>) => void;
  inputRef?: React.RefObject<HTMLInputElement>;
  error?: string;
  helperText?: string;
  multiline?: boolean;
  maxRows?: number;
  minRows?: number;
  children: ReactNode;
  inputProps?: Record<string, unknown>;
}

function EditableField({
  isEditing,
  isEditMode,
  onStartEdit,
  value,
  onChange,
  inputRef,
  error,
  helperText,
  multiline = false,
  maxRows = 10,
  minRows = 3,
  children,
  inputProps,
}: EditableFieldProps) {
  return (
    <Box
      sx={{
        cursor: isEditMode ? 'pointer' : 'default',
        borderBottom: isEditMode ? '1px dotted rgba(0, 0, 0, 0.2)' : 'none',
        transition: 'all 0.2s ease',
        p: isEditMode ? 2 : 1,
        mb: 2,
        borderRadius: 1,
        '&:hover': isEditMode
          ? {
              bgcolor: 'rgba(0, 0, 0, 0.02)',
            }
          : {},
      }}
      onClick={() => isEditMode && onStartEdit()}
    >
      {isEditing ? (
        <TextField
          ref={inputRef}
          fullWidth
          multiline={multiline}
          minRows={minRows}
          maxRows={maxRows}
          value={value}
          onChange={onChange}
          variant="outlined"
          size="small"
          autoFocus
          error={!!error}
          helperText={helperText}
          sx={{
            '& .MuiInputBase-root': {
              padding: '12px 14px',
            },
            '& .MuiOutlinedInput-root': {
              backgroundColor: 'white',
            },
          }}
          {...inputProps}
        />
      ) : (
        children
      )}
    </Box>
  );
}

export default EditableField;
